///<reference path="../../typings/index.d.ts" />
import * as React from "react";
import { CardHeader, BigBlue, Clickable } from "./helpers";

interface ITitleProps extends React.Props<any> {
    title: string;
}

const Title = ({title = "test title"}: ITitleProps) =>
            <CardHeader>
                <Clickable>
                    <BigBlue>{title}</BigBlue>
                </Clickable>
            </CardHeader>;

export default Title;        


